/** Media Queries **/
.dashboard {
  background-color: #f3f4f6;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
}
@media (min-width: 768px) {
  .dashboard .principal {
    overflow-y: scroll;
  }
}
.dashboard .formulario input:not([type=submit]) {
  background-color: #d9d9d9;
}
.dashboard .formulario input[type=submit] {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 1.5rem;
}
@media (min-width: 768px) {
  .dashboard .formulario input[type=submit] {
    width: auto;
  }
}
.dashboard .formulario input[type=submit]:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.dashboard .no-proyecto {
  text-align: center;
}
.dashboard .listado-proyectos {
  list-style: none;
  padding: 0;
  margin-top: 5rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .dashboard .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .dashboard .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.dashboard .listado-proyectos .proyecto {
  background-color: #564ccf;
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 3rem;
  line-height: 2;
  border-radius: 1.5rem;
  text-align: center;
}
.dashboard .listado-proyectos .proyecto:hover {
  background-color: #3b31b8;
}
.dashboard .listado-proyectos .proyecto a {
  color: #FFFFFF;
}
.dashboard .enlace {
  color: #4338CA;
}
.dashboard .enlace:hover {
  color: #28217b;
  border-bottom: 1px solid black;
}

.contenido {
  padding: 2rem;
}
.contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}

.sidebar {
  opacity: 0;
  height: 0;
  background-color: #4338CA;
  transition-property: opacity;
  transition-duration: 500ms;
  overflow: hidden;
}
.sidebar.visible {
  opacity: 1;
  padding: 2rem;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
@media (min-width: 768px) {
  .sidebar.visible {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    opacity: 1;
    height: 100%;
    padding: 2rem;
    background-color: #4338CA;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 3rem;
  width: 3rem;
}
.sidebar .contenedor-sidebar .cerrar-menu:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  padding: 1rem;
  transition: all 300ms linear;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.5rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover {
  background-color: #211b67;
  border-radius: 1rem;
}
.sidebar .sidebar-nav .activo {
  background-color: #211b67;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    text-align: left;
  }
}
.sidebar .cerrar-sesion-mobile {
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile {
    display: none;
  }
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  font-size: 1.5rem;
  padding: 1.5rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile .cerrar-sesion {
    width: auto;
  }
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion:hover {
  background-color: #066981;
  cursor: pointer;
}

.barra-mobile {
  background-color: #4338CA;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile h1 {
  margin: 0;
  color: #FFFFFF;
}
.barra-mobile .menu img {
  cursor: pointer;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    background-color: #FFFFFF;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra p {
  margin: 0;
  font-size: 1.5rem;
}
.barra p span {
  font-weight: 700;
  color: #4338CA;
}
.barra .cerrar-sesion {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  font-size: 1.5rem;
  padding: 1.5rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    width: auto;
  }
}
.barra .cerrar-sesion:hover {
  background-color: #066981;
  cursor: pointer;
}

.contenedor-nueva-tarea {
  margin-top: 5rem;
  display: flex;
  gap: 2rem;
  justify-content: center;
}
.contenedor-nueva-tarea .agregar-tarea, .contenedor-nueva-tarea .eliminar-proyecto {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .contenedor-nueva-tarea .agregar-tarea, .contenedor-nueva-tarea .eliminar-proyecto {
    width: auto;
  }
}
.contenedor-nueva-tarea .agregar-tarea:hover, .contenedor-nueva-tarea .eliminar-proyecto:hover {
  background-color: #066981;
  cursor: pointer;
}

.listado-tareas {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
}
.listado-tareas .no-tareas {
  text-align: center;
}

.tarea {
  padding: 0.5rem 2rem;
  border-bottom: 2px solid #e4e7eb;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.tarea:last-of-type {
  border: none;
}
.tarea p {
  font-size: 1.4rem;
}
.tarea .opciones {
  display: flex;
  gap: 1.5rem;
}
.tarea .opciones button {
  border: none;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-weight: 900;
  border-radius: 1rem;
}
.tarea .opciones button:hover {
  cursor: pointer;
}
.tarea .opciones .completa {
  background-color: #2563EB;
  color: #FFFFFF;
}
.tarea .opciones .pendiente {
  background-color: #F59E0B;
  color: #FFFFFF;
}
.tarea .opciones .eliminar-tarea {
  background-color: #f60000;
  color: #FFFFFF;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .formulario {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  border-top-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 1000ms;
  transition-timing-function: ease-in-out;
  opacity: 0;
}
.modal .formulario legend {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
  text-align: center;
}
.modal .formulario .opciones {
  display: flex;
  justify-content: space-between;
}
.modal .formulario .opciones .submit-tarea, .modal .formulario .opciones .submit-proyecto {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .modal .formulario .opciones .submit-tarea, .modal .formulario .opciones .submit-proyecto {
    width: auto;
  }
}
.modal .formulario .opciones .submit-tarea:hover, .modal .formulario .opciones .submit-proyecto:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.modal .formulario .opciones .cerrar-modal {
  background-color: #F59E0B;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .modal .formulario .opciones .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .opciones .cerrar-modal:hover {
  background-color: #c57f08;
  cursor: pointer;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}

.filtros {
  margin-top: 5rem;
  background-color: #d6d9e0;
  padding: 2rem;
  border-radius: 1.5rem;
}
.filtros .filtros-inputs {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .filtros .filtros-inputs {
    flex-direction: row;
    gap: 3rem;
  }
}
.filtros .filtros-inputs h2 {
  font-size: 2.4rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .filtros .filtros-inputs h2 {
    margin: 0;
  }
}
.filtros .filtros-inputs .campo {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .filtros .filtros-inputs .campo {
    margin-bottom: 0;
  }
}
.filtros .filtros-inputs .campo input[type=radio] {
  accent-color: #4338CA;
  width: 1.5rem;
  height: 1.5rem;
}